<template>
<h3>-----------------------------value  -----------------------------</h3>
  <div class="basic">
    <h1>hello, {{title}}</h1>
    <h3>{{title}}    </h3>
    <p>{{message}}</p>
    <p v-html ='message'></p>
    
  </div>
  <div>
  <input type="text" v-model ="nickname" />
  </div>
  <div>
  <input type="number" v-model.number ="age" />
  </div>
  <div>
    <textarea v-model = "message" id="" cols="30" rows="10"></textarea>
  </div>
  <div>
    <select name="city" v-model = "city">
      <option value="01"></option>
      <option value="02" selected></option>
      <option value="03"></option>
      <option value="04"></option>
    </select>
  </div>
  <br/>
  <div>
    <label><input type="checkbox" v-model = "cbox">{{cbox}}</label>
    <br/>
    <br/>
    <label><input type="checkbox" v-model = "cbox2" true-value = ""
    false-value = "">{{cbox2}}</label>
  </div>
  <div>
    <p> ?</p>
    <label><input type="checkbox" v-model = "goodfood" value = ""></label>
    <label><input type="checkbox" v-model = "goodfood" value = ""></label>
    <label><input type="checkbox" v-model = "goodfood" value = ""></label>

    <p>   {{goodfood}}  </p>

    <p> ?</p>
    <label><input type="checkbox" v-model = "badfood" value = ""></label>
    <label><input type="checkbox" v-model = "badfood" value = ""></label>
    <label><input type="checkbox" v-model = "badfood" value = ""></label>

    <p>   {{badfood}} </p>
  </div>
  <div>
    <p> ?</p>
    <label for=""><input type="radio" v-model = "gender" value = ""></label>
    <label for=""><input type="radio" v-model = "gender" value = ""></label>
    <p> {{gender}} </p>
  </div>
  <h3>----------------------------- -----------------------------</h3>
  <div>
    <img v-bind:src="imgSrc" alt="" v-bind:title = "">
  </div>
  <div>
    <button v-bind:disabled = "show1"></button>
    <button v-bind:disabled = "show2"></button>
  </div>
  <div>
    <button v-bind:style = "btn1"></button>
    <button v-bind:style = "btn2"></button>
  </div>
  <h3>-----------------------------(v-for, v-if, v-else)-----------------------------</h3>
  <div>
    <table >
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-bind:key = "i" v-for = "(item, i) in products">
          <td>{{item.name}}</td>
          <td>{{item.price}}</td>
          <td>{{item.category}}</td>
          <td>{{item.delivery}}</td>
        </tr>
      </tbody>
    </table>
  </div>

  <div>
    <!-- v-if    -->
    <p v-if = "true">if </p>
    <p v-if = "false">if </p>
    <!-- v-show   diplay:none      -->
    <p v-show = "true">show </p>
    <p v-show = "flase">show </p>
  </div>

   <h3>-----------------------------(v-on / @)-----------------------------</h3>

  <div>
    <button v-on:click = "increaseCounter"> ()</button>
    <button @:click = "decreaseCounter"> ()</button>
    <p>counter : {{counter}}</p>
    <button @:click = "increaseCounter(), showMsg()">  </button>
    <button @:click = "decreaseCounter(), showMsg()">  </button>
    <br>
    <input type="number" v-model = "countValue">
    <button @:click = "applyCounter"> </button>
  </div>

  <br><br>

  <div>
    <select v-model = "cityValue" @change = "changeCity" >
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
    </select>
  </div>

  <br><br>

  <div>
    <input type="text" v-model = "emailValue" @input = "changeEmail" placeholder = " .">
    <!-- <p>{{changeEmail}}/{{errEmail}}</p> -->
    <p v-if = "errEmail">{{errEmail}}</p>
  </div>

  <br><br>

  <div>
    <input type="text" v-model = "pwdValue1" @input = "changePwd1" placeholder = " ."><br><br>
    <input type="text" v-model = "pwdValue2" @input = "changePwd2" placeholder = " ."><br>
    <p v-if = "errPwd">{{errPwd}}</p>
  </div>

  <br>

   <h3>----------------------------- computed-----------------------------</h3>

  <div>
    <p>{{hello()}}</p>
    <p>{{hello()}}</p>
    <p>{{hello()}}</p>
    <p>{{hello2}}</p>
    <p>{{hello2}}</p>
    <p>{{hello2}}</p>
  </div>

<br>

<div>
   : <input type="text" v-model = "lastName" @input = "changeLastName"><br>
   : <input type="text" v-model = "firstName" @input = "changeFirstName"><br>
  <p>method : {{methodFullName()}}</p>
  <p>method : {{methodFullName()}}</p>

  <p>computed : {{computeFullName}}</p>
  <p>computed : {{computeFullName}}</p>

  <p>fullName : {{fullName}}</p>
  <p>fullName : {{fullName}}</p>
</div>

   <h3>-----------------------------watch-----------------------------</h3>
  <div>
    <h4>user_info : {{userInfo}}</h4>
    <input type="text" v-model = "userName">
    <input type="text" v-model = "userAge">
  </div>


  <br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br>
</template>

<script>
export default {
  name: 'BasicView',
  components: {
    
  },
  data() {
    return {
      title: 'IT',
      message: '<b>IT</b> <b><span>5</span></b>  ',
      nickname: '',
      age: 22+4 ,
      city: '01' ,
      cbox: false ,
      cbox2: '' ,
      goodfood: [] ,
      badfood: [] ,
      gender: '' ,
      imgSrc: "https://item.kakaocdn.net/do/009619490db024d6a8acf277cb93a6c58f324a0b9c48f77dbce3a43bd11ce785" ,
      tooltip: '' ,
      show1: true ,
      show2: false ,
      btn1: {
        backgroundColor: 'skyblue',
        color: 'yellow',
        fontSize: '16px',
        borderRadius: '8px'

      } ,
      btn2: {
        backgroundColor: 'pink',
        color: 'green',
        fontSize: '16px',
        borderRadius: '8px'
      } ,
      products: [
        {"name" : "1", "price" : "25000", "category" : "pc 1", "delivery" : "3000"},
        {"name" : "", "price" : "35000", "category" : "pc 2", "delivery" : "3000"},
        {"name" : " ", "price" : "45000", "category" : "pc 3", "delivery" : "3000"},
      ],
      counter: 0 ,
      countValue : 10 ,
      cityValue: '' ,
      emailValue: '' ,
      errEmail: '' ,
      pwdValue1: '' ,
      pwdValue2: '' ,
      errPwd: ' ' ,
      lastName:'' ,
      firstName:'' ,
      fullName:'' ,
      userName:'' ,
      userAge: '17' ,
      userInfo: '' ,

    }
  },
  watch:{
    userName(){
      this.userInfo = this.userName + '(' + this.userAge + ')'
    },
    // userAge(){
    //   this.userInfo = this.userName + '(' + this.userAge + ')'
    // },
  },
  computed: {
    hello2() {
      console.log('hello2 ');
      return ', ';
    },
    computeFullName() {
      console.log('computeFullName ');
      return this.lastName + this.firstName;
    }
  },
  setup() {
    
  },
  created() {
    
  },
  mounted() {
    
  },
  unmounted() {
    
  },
  
  methods: {
    increaseCounter(){
      this.counter = this.counter + 1
    },
    decreaseCounter(){
      this.counter = this.counter - 1
    },
    applyCounter(){
      this.counter = this.countValue
    },
    showMsg(){
      alert(' =>' + this.counter)
    },
    changeCity(){
      alert('  : ' + this.cityValue)
    },
    changeEmail(){
      console.log('sss')
      //  
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
      // this.errEmail = this.emailValue
      if(this.emailValue === '' || emailPattern.test(this.emailValue)){
        this.errEmail = ''
        console.log('ok' + this.emailValue)
      } else {
        this.errEmail = '  '
        console.log('err' + this.emailValue)
      }
    },
    changePwd1(){
      if (this.pwdValue1 === ''){
        this.errPwd = ' .'
      } else if (this.pwdValue2 === ''){
        this.errPwd = ' .'
      } else if (this.pwdValue1 === this.pwdValue2){
        this.errPwd = ''
      } else {
        this.errPwd = '  .'
      }
    },
    changePwd2(){
      if (this.pwdValue1 === ''){
        this.errPwd = ' .'
      } else if (this.pwdValue2 === ''){
        this.errPwd = ' .'
      } else if (this.pwdValue1 === this.pwdValue2){
        this.errPwd = ''
      } else {
        this.errPwd = '  .'
      }
    },
    hello(){
      console.log('hello()');
      return ', .';
    },
    changeLastName() {
      // lastName   fullName 
      console.log('changeLastName ');
      this.fullName = this.lastName + this.firstName;
    },
    changeFirstName() {
      // firstName   fullName 
      console.log('changeFirstName ');
      this.fullName = this.lastName + this.firstName;
    },
    methodFullName() {
      //  fullName 
      console.log('methodFullName ');
      return this.lastName + this.firstName;
    },
  }
}
</script>

<style scoped>
table{
  border-collapse: collapse;
  width: 100%;

}
td, th{
  border: 1px solid #ddd;
  text-align: center;
  padding: 8px;
}
th{
  text-align: center;
  font-weight: 600;
}
</style>