<template>
  <div class="">
    <!--   -->
     <h1> </h1>
      <ul>
        <li v-for = "(member, index) in members" :key = "index">
          <p>{{index}}  : {{member.name}},  : {{member.age}},  : {{member.job}}
            <button @click = "removeMember(index)"></button>
          </p>
        </li>
      </ul>
  </div>
  <div>
     : <input type="text" placeholder="" v-model = "editName"><br>
     : <input type="text" placeholder="" v-model = "editAge"><br>
     : <input type="text" placeholder="" v-model = "editJob"><br>
    <button @click = "addMember"></button>
  </div>
</template>

<script>
import { useStore } from 'vuex'
import { computed, ref } from 'vue'

export default {
  name: 'MemberView',

  setup() {
    const store = useStore()
    const members = computed ( () => store.state.members)

    const editName = ref('')
    const editAge = ref('')
    const editJob = ref('')

    const addMember = () => {
      const data = {
        name : editName.value,
        age : editAge.value,
        job : editJob.value
      }

    store.commit('addMember', data)
    editName.value = ''
    editAge.value = ''
    editJob.value = ''

    console.log('' + store.state.members.name)
    }

    const removeMember = (index) => {
      store.commit('removeMember', index)
    }

    return {members, addMember, removeMember, editName, editAge, editJob}
  },

  
}
</script>

