<template>
  <div class="pracSelf">
    <input type="text" value= "333"><br>
    <!-- <input type="text" v-bind = "nick"><br> -->
    <input type="text" v-model = "nick"><br>
    <input type="number" v-model = "age"><br>
    <textarea cols="30" rows="10"> </textarea><br>
    <textarea v-model = "mmessage" id="" cols="30" rows="10"></textarea><br>
    <br>
    <select name="" id="">
        <option value="01"></option>
        <option value="02" selected></option>
        <option value="03"></option>
        <option value="04"></option>
    </select>
    <br>
    <select v-model = "inCity">
        <option value="01"></option>
        <option value="02"></option>
        <option value="03"></option>
        <option value="04"></option>
    </select>

    <br>

        <input type="checkbox" id = "cbtn_agree" name = "cbtn_agree">
        <label for="cbtn_agree"></label><br>

        <label><input type="checkbox" v-model = "agree"></label><br>
        <label><input type="checkbox" v-model = "agree2"
            true-value = "" false-value = "">{{agree2}}</label><br>
        
        <div>
        <p>    .</p>
            <label><input type="checkbox" v-model = "pizza" value = ""></label><br>
            <label><input type="checkbox" v-model = "pizza" value = ""></label><br>
            <label><input type="checkbox" v-model = "pizza" value = ""></label><br>
        <p>   {{pizza}}</p>
        </div>

        <br>

        <div>
            <p>  ?</p>
            <label><input type ="radio" v-model = "sweet_sour_pork" value = ""></label>
            <label><input type ="radio" v-model = "sweet_sour_pork" value = ""></label>
            <!-- <label><input type ="radio" v-model = "sweet_sour_pork" value = ""></label> -->
            <p> <span style="color:red; font-weight: 800;">{{ sweet_sour_pork }} .</span></p>
        </div>
        <br>
    <h3>---------------------------------------------------------------- </h3>
    <div>
      <img src='https://i.pinimg.com/736x/39/a3/86/39a386434b6882e1f6baf9b3f7daf0da.jpg' title ="  "><br>
      <img v-bind:src="imgSrc" alt="" v-bind:title = "imgTitle"><br>
    </div>
    <br>
        <button :disabled = "btn_01" :style = "color_blue"></button>
        <button :disabled = "btn_02" :style = "color_red"></button>

    <h4> ---------------------------------- v-for----------------------------------</h4>
    <table>
        <thead>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
        </thead>
        <tbody>
            <tr v-for = "(item, index) in goods" v-bind:key = "index">
                <td>{{item.name}}</td>
                <td>{{item.price}}</td>
                <td>{{item.category}}</td>
                <td>{{item.delivery}}</td>
            </tr>
        </tbody>
    </table>

    <br>
    <h4> ---------------------------v-if, v-else, v-else if-----------------------------</h4>
    <p v-if = "true"></p>    
    <p v-if = "false"></p>    

    <br>

    <div>
        <label for="">  : <input type="number" v-model = "kor"></label><br>
        <label for="">  : <input type="number" v-model = "eng"></label><br>
        <label for="">  : <input type="number" v-model = "math"></label><br>
        <br>
            <p v-if = "average >= 90">A({{ average }})</p>
            <p v-else-if = "average >= 80">B({{ average }})</p>
            <p v-else-if = "average >= 70">C({{ average }})</p>
            <p v-else-if = "average >= 60">D({{ average }})</p>
            <p v-else>F({{ average }})</p><br>
    </div>

    <h4>--------------------------- v-on // @------------------------------</h4>
    <br>

    <div>
        <button v-on:click = "showScore"> </button>
    </div>
   
    <br><br><br>
 
    <h4>---------------------------------------------------------</h4>

    <div>
        <label><input type="number" v-model = "ndan"></label>
        <button v-on:click = "calculate_dan"></button>
        <p>{{ndan}}</p>
        <p v-for = "(result, index) in googodan" :key = "index">{{ result }}</p>
    </div>

  <br><br><br>

    <div>
      <h3> </h3>
      <label fo r= "dan">   (1-9): </label>
      <input type = "number" v-model = "dan" id = "dan" min = "1" max = "9" />
      
      <button @click = "generateGugudan"> </button>
      
      <div v-if = "gugudan.length">
        <h2>{{ dan }} </h2>
        <ul>
          <li v-for = "(value, index) in gugudan" :key = "index">
            {{ dan }} x {{ index + 1 }} = {{ value }}
          </li>
        </ul>
      </div>
    </div>
<br><br><br><br>
    <div>
      <select name="" id="">
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value="" selected></option>
        </select>
        <br>
        <select v-model = "city" @change = "changeCity">
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
        <p>{{ message }}</p>
      </div> 

      <br>

      <div>
        <p><label><input type="text" placeholder=" ."
          v-model = "emailValue" @input = "changeEmail"></label><span>{{ msgEmail }}</span></p>


      <!-- 8     @#      -->
        <p><label><input type="text" 
          v-model = "pwd1" 
          @input = "changePwd" placeholder = " ."></label>
          <span>{{ msgPwd1 }}</span></p>

        <p><label> <input type="text" 
          v-model = "pwd2" 
          @input = "changePwd2" placeholder = " ."></label>
          <span>{{ msgPwd2 }}</span></p>
      </div>

      <br>

      <div>
      <label> : <input type="text" v-model = "familyName1" @input = "makeFullName1"></label>
      <label> : <input type="text" v-model = "name1"  @input = "makeFullName1"></label>
      <p>(method) : {{ fullName1 }}</p>
    </div>

    <br><br>

    <div>
      <label> : <input type="text" v-model = "familyName2"></label>
      <label> : <input type="text" v-model = "name2"></label>
      <p>(computed) : {{ makeFullName2 }}</p>
    </div>

    <br><br>

    <div>
      <label> : <input type="text" v-model = "familyName3"></label>
      <label> : <input type="text" v-model = "name3"></label>
      <p>(watch) : {{ fullName3 }}</p>
    </div>




  <br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br>
  </div>
</template>

<script>
export default {
  name: 'PracSelf',
  components: {
    
  },
  data() {
    return {
      nick: ' ',
      age: 30,
      mmessage: '    ?',
      inCity: '03',
      agree: false,
        agree2: '',
        pizza: [],
        sweet_sour_pork: '',
        imgSrc: 'https://item.kakaocdn.net/do/009619490db024d6a8acf277cb93a6c58f324a0b9c48f77dbce3a43bd11ce785',
        imgTitle: '  ',
        btn_01: true,
        btn_02: false,
        color_blue: {color: 'blue', fontSize: '24px'},
        color_red: {color: 'red', fontSize: '24px'},
        goods: [
        {"name": "", "price": 10000, "category": "PC", "delivery": 3000},
        {"name": "", "price": 20000, "category": "PC", "delivery": 2000},
        {"name": "", "price": 800000, "category": "PC", "delivery": 30000},
      ],
      score: 0,
      kor: 0,
      eng: 0,
      math: 0,
      average: 0,
      dan: 1,
      ndan: 1,
      gugudan: [],
      googodan: [],
      city: '',
      message: ' !',
      emailValue: '',
      msgEmail: ' ',
      pwd1: '',
      pwd2: '',
      msgPwd1: ' ',
      msgPwd2: '   ',

      familyName1: '',
      name1: '',
      fullName1: '',
      familyName2: '',
      name2: '',
      familyName3: '',
      name3: '',
      fullName3: '',
    }
  },
  watch: {
    familyName3 () {
      this.fullName3 = this.familyName3 + ' - ' + this.name3
    },
    name3 () {
      this.fullName3 = this.familyName3 + '-' + this.name3
    }
  },
  computed: {
    makeFullName2 () {
      return this.familyName2 + '-' + this.name2
    }
  },
  
  methods: {
    showMessage(){
        alert(' ')
    },
    showScore(){
      this.average = (this.kor + this.eng + this.math) / 3
    },

    generateGugudan() {
      this.gugudan = []; //    
      for (let i = 1; i <= 9; i++) {
        this.gugudan.push(this.dan * i)
      }
    },

    calculate_dan(){
      this.googodan = []
      for (let n = 2; n < 10; n++){
          this.googodan.push(`${this.ndan} * ${n} = ${this.ndan * n}`)
      }
    },

    checkPwd (pwd) {
      let msg = ''
    //   
      const len = this.pwd.length
    //   
      const hasWord = /[a-zA-Z]/.test(pwd)
    //  
    const hasNum = /[\d]/.test(pwd)
    //  
    const hasForbid = /[@#]/.test(pwd)
    if (len < 8) {
      msg = " 8  ."
    } else if (!hasWord) {
      msg = "   ."
    } else if (!hasNum) {
      msg = "   ."
    } else if (hasForbid) {
      msg = "@, #  ."
    } else {
      msg = ""
    }
    return msg
  },
     
  changePwd () {
    let msg = this.checkPwd(this.pwd1)
    if (msg === '') {
      msg = ' '
    }
    this.msgPwd1 = msg
  },
  changePwd2() {
    let msg = this.checkPwd(this.pwd2)
    if (msg === '') {
      if (this.pwd1 === this.pwd2) {
        msg = ' .'
      } else {
        msg = '  .'
      }
      this.msgPwd2 = msg
      }
    },

  changeEmail() {
    // this.emailValue    
    const emailExp = /^[^\s@]+@[^\s@]+\.[^\s@]+$/ 
    //    :  + @ +  + . + 
    if (this.emailValue.length == 0) {
      this.msgEmail = ' .'
    } else if (emailExp.test(this.emailValue) == true) {
      this.msgEmail = '  .'
    } else {
      this.msgEmail = '   .'
    }
  },

  changeCity(){
    switch(this.city){
      case '' :
        this.message = ' !'
        break
      case '' :
        this.message = '! '
        break
      case '' :
        this.message = ' !'
        break
      case '' :
        this.message = ' !'
        break
      case '' :
        this.message = ' !'
        break
      default :
      this.message = 'hello nice to meet to you'
        break
    }
  },

  makeFullName1 () {
    this.fullName1 = this.familyName1 + '-' + this.name1
  }

  }
}
</script>

<style scoped>
  table{
        width: 100%;
        border-collapse: collapse;
    }
    td, th{
        border: 1px solid gray;

    }
    div> ul> li{
      list-style: none;

    }
</style>