<template>
  <div class="">
    <!--   -->
     <h1> </h1>
     <div>
      <p><b> : </b> {{user.name}} </p>
      <p><b> : </b> {{user.email}} </p>
     </div>
     <button @click = "saveUserData1">  </button>
     <button @click = "saveUserData2">  </button>
     <br>
     <input type="text" v-model = "editName" />
     <input type="text" v-model = "editEmail" />
     <button @click = "saveUser"></button>


  </div>
</template>

<script>
import { useStore } from 'vuex'
import { computed, ref } from 'vue'

export default {
  name: 'UserView',
  setup() {
    const store = useStore()

    //vuex  user  
    const user = computed( () => store.state.user)

    const editName = ref('')
    const editEmail = ref('')

    // 1 vuex  
    const saveUser = () => {
      const data = {name: editName.value, email: editEmail.value}
      store.commit('setUser', data)
    }

    const saveUserData1 = () => {
      const data = {name: '', email: 'ceo.sea.com'}
      store.commit('setUser', data)
    }

    // 2 vuex  
    const saveUserData2 = () => {
      const data = {name: '', email: 'thief@france.com'}
      store.commit('setUser', data)
    }
    return{
      user, saveUserData1, saveUserData2, saveUser, editName, editEmail
    }
  }
}
</script>

